.action-bar {
    position: fixed;
    z-index: 100;
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    height: 3.4rem;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    background: #1ba3e8;
    color: #fff;
    font-size: 1.28rem;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
.iconfont {
    display: inline-block;
    line-height: 3.4rem;
    font-size: 1.5rem;
}
.menu {
    padding: 0 2rem 0 1rem;
}
.msg {
    padding: 0 .5rem;
}
.setting {
    padding: 0 1rem 0 .5rem;
    vertical-align: middle;
}



