/*---------- story  ----------*/
.story-list {
  padding-top: .8rem;
  background-color: #f2f2f2;
}
.story-item {
  display: inline-block;
  margin-top: .8rem;
  width: 100%;
}
.story-item a{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: start;
        justify-content: flex-start;
    -ms-flex-align: start;
        align-items: flex-start;
    margin-left:.8rem;
    margin-right: .8rem;
    background: #fff;
    padding:.6rem;
    -webkit-box-shadow: .1rem .2rem .4rem #ddd;
            box-shadow: .1rem .2rem .4rem #ddd;
    border-radius: .1rem;
    height: 6rem;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    text-decoration: none;
    color: #000;
}
.story-img{
    height: 4.8rem;
    width: 4.8rem;
}
.story-title{
    margin-left:.5rem;
    margin-top: .5rem;
    font-weight: normal;
}



